<template>
  <div
    class="rounded-button"
    :class="sex"
    @click="changeSex(sexKey, oppo)"
  >
    <font-awesome-icon
      :class="icon"
      :icon="icon"
    />
  </div>
</template>

<script>
import { db } from '@/firebase'

export default {
  name: 'RoundedButtonComponent',
  props: {
    sex: {
      type: String,
      required: true
    },
    icon: {
      type: String,
      required: true
    },
    sexKey: {
      type: String,
      required: true
    },
    oppo: {
      type: String,
      required: true
    }
  },
  data () {
    return {
    }
  },
  methods: {
    changeSex: function (id, subject) {
      subject === 'F' ? subject = 'M' : subject = 'F'
      return db.collection('names').doc(id).update({
        sex: subject
      })
    }
  }
}
</script>

<style lang="scss">
.rounded-button.M {
    & svg.male path {
      fill: #0457bd;
      opacity: 0.75;
    }
}
.rounded-button.F {
    & svg.female path {
      fill: #ff00eb;
      opacity: 0.6;
    }
}

</style>
