<template>
  <div class="nav">
    <div class="link-list">
      <router-link
        class="link-list__item"
        to="/home"
      >
        <font-awesome-icon icon="birthday-cake" /> Home <span
          v-if="userId"
          class="counter"
        >{{ names.length }}</span>
      </router-link>
    </div>
    <div class="link-list">
      <router-link
        class="link-list__item"
        to="/list"
      >
        <font-awesome-icon icon="list-alt" /> {{ $t("nav.myList") }} <span
          v-if="userId"
          class="counter"
        >{{ Object.keys(listed).length }}</span>
      </router-link>
    </div>
    <div class="link-list">
      <router-link
        class="link-list__item"
        to="/information"
      >
        <font-awesome-icon icon="info-circle" /> {{ $t("nav.information") }}
      </router-link>
    </div>
    <div class="link-list">
      <router-link
        class="link-list__item"
        to="/groups"
      >
        <font-awesome-icon icon="users" /> {{ $t("nav.groups") }} <span
          v-if="userId"
          class="counter"
        >{{ groups.length }}</span>
      </router-link>
    </div>
    <div class="link-list">
      <ChangeLang />
    </div>
    <div class="link-list">
      <a
        class="link-list__item"
        v-if="userId"
        @click="logout"
      >
        <font-awesome-icon icon="user" /> {{ $t("nav.logout") }}
      </a>

      <router-link
        class="link-list__item"
        v-else
        to="/login"
      >
        <font-awesome-icon icon="user" /> {{ $t("nav.login") }}
      </router-link>
    </div>
  </div>
</template>
<script>
import { db } from '@/firebase'
import ChangeLang from '@/components/ChangeLang.vue'
import { mapState } from 'vuex'
import { firebase } from '@firebase/app'
import store from '../store.js'

export default {
  components: {
    ChangeLang
  },
  data () {
    return {
      names: [],
      groups: [],
      listed: []
    }
  },
  firestore () {
    return {
      listed: db.collection('listed').doc(this.userId),
      groups: db.collection('groups'),
      names: db.collection('names')
    }
  },
  methods: {
    logout: function () {
      firebase.auth().signOut().then(() => {
        store.commit('LOGOUT')
        this.$router.replace('login')
      })
    }
  },
  computed: {
    ...mapState({
      userId: state => state.userId
    })
  }
}

</script>

<style lang="scss">
.link-list {
    &__item {
        cursor: pointer;
    }
}
</style>
