import Vue from 'vue'
import Vuex from 'vuex'
import { db } from '@/firebase'
import { firebase } from '@firebase/app'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    showManOnly: false,
    showWomanOnly: false,
    names: [],
    selected: [],
    userId: '',
    preferredLanguage: 'nl',
    userDisplayName: ''
  },
  mutations: {
    setNames: state => {
      const names = db.collection('names')
      state.names = names
    },
    listed: (state, list) => {
      state.selected = list
    },
    removeListed: (state, item) => {
      state.selected.splice(item, 1)
    },
    addListed: (state, item) => {
      state.selected.push(item)
    },
    LOGOUT (state, userId) {
      state.userId = ''
      state.userDisplayName = ''
    },
    SET_USER_ID (state, userId) {
      state.userId = userId
    },
    SET_USER_DISPLAY_NAME (state, userDisplayName) {
      state.userDisplayName = userDisplayName
    },
    SET_USER_PREFERRED_LANGUAGE (state, preferredLanguage) {
      state.preferredLanguage = preferredLanguage
    }
  },
  actions: {
    setNames: context => {
      context.commit('setNames')
    },
    setListed: context => {
      context.commit('listed')
    },
    removeListed: context => {
      context.commit('removeListed')
    },
    addListed: context => {
      context.commit('addListed')
    },
    login ({ commit, state }, { email, password }) {
      return new Promise((resolve, reject) => {
        firebase.auth().signInWithEmailAndPassword(email, password)
          .then((user) => {
            resolve(user)
          })
          .catch((err) => {
            reject(err)
          })
      })
    },
    logout ({ commit, state }) {
      return new Promise((resolve, reject) => {
        firebase.auth().signOut()
          .then(() => {
            commit('LOGOUT')
            resolve()
          })
          .catch((err) => {
            reject(err)
          })
      })
    },
    checkUserStatus ({ commit, state }) {
      return new Promise((resolve, reject) => {
        firebase.auth().onAuthStateChanged((user) => {
          if (user) {
            commit('SET_USER_ID', user.uid)
            resolve(user.uid)
          } else {
            reject(new Error('User is not logged in.'))
          }
        })
      })
    }

  },
  getters: {
    getListed: state => {
      return state.selected
    }
  }
})
