<template>
  <div class="bulk-actions">
    <div
      class="bulk-actions__set-listed bulk-actions__action"
      :class="{ disabled: listed.length == 0 }"
      @click="setListed(listed, userId)"
    >
      <font-awesome-icon icon="plus-square" /> {{ $tc('set_listed', listed.length) }}
    </div>
    <div
      class="bulk-actions__delete-all bulk-actions__action"
      :class="{ disabled: listed.length == 0 }"
      @click="deleteAll(listed)"
    >
      <font-awesome-icon icon="trash-alt" /> {{ $tc('delete_selected', listed.length) }}
    </div>
  </div>
</template>

<script>
import { db } from '@/firebase'
import store from '../store.js'
import { mapState } from 'vuex'

export default {
  name: 'BulkActions',
  components: {
  },
  data () {
    return {

    }
  },
  computed: {
    listed () {
      return store.state.selected
    },
    ...mapState({
      userId: state => state.userId
    })
  },
  methods: {
    setListed: (listed, id) => {
      if (listed.length === 0) {
        return
      }
      listed.forEach(element => {
        const listedValue = {
          [element]: true
        }
        console.log('element', element)
        console.log('listedValue', listedValue)
        db.collection('listed').doc(id).set(listedValue, { merge: true }).then(() => {
          store.commit('listed', [])
        })
      })
    },
    deleteAll: (listed) => {
      if (listed.length === 0) {
        return
      }

      listed.forEach(element => {
        db.collection('names').doc(element).delete().then(() => {
          console.log('succes')
          store.commit('listed', [])
        })
          .catch((error) => {
          // The document probably doesn't exist.
            console.error('Error updating document: ', error)
          })
      })
    }
  }
}
</script>

