<template>
  <div class="group__add-group">
    <button
      hidden="true"
      @click.prevent="saveData"
    >
      send!
    </button>
    <div class="add-group-form">
      <span class="add-group-form__name">
        <input
          v-model="newName"
          placeholder="groupname"
        >
      </span>
      <span class="add-group-form__add">
        <button @click="addName">
          <font-awesome-icon icon="user-plus" />
        </button>
      </span>
    </div>
  </div>
</template>

<script>
import { db } from '@/firebase'

export default {
  name: 'AddGroups',
  components: {},
  data () {
    return {
      names: [],
      newName: null
    }
  },
  firestore () {
    return {
      names: db.collection('groups')
    }
  },
  computed: {},
  methods: {
    addName: function (element = null) {
      if (this.newName) {
        db.collection('groups').add({
          name: this.newName,
          names: [],
          timestamp: new Date()
        })
        this.newName = ''
      } else {
        console.error('Fill in all the fields dummy')
      }
    }
  }
}
</script>

