<template>
  <div class="group__container">
    <div
      :key="group['.key']"
      v-for="group in groups"
      class="group__list-item"
    >
      <div
        class="group__name"
      >
        <span
          @click="editGroup($event.target)"
        >{{ group.name || '-' }} </span>
        <input
          v-model="group.name"
          style="display: none"
          v-autowidth="{maxWidth: '960px', minWidth: '20px'}"
          @keydown.enter="saveGroup(group['.key'], $event.target, 'name')"
          @blur="saveGroup(group['.key'], $event.target, 'name')"
        >
      </div>

      <div
        class="group__delete"
      >
        <div
          class="rounded-button"
          @click="deleteGroup(group)"
        >
          <font-awesome-icon icon="trash-alt" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { db } from '@/firebase'

export default {
  name: 'Groups',
  components: {

  },
  filters: {
  },
  methods: {
    deleteGroup: function (name) {
      db.collection('groups').doc(name['.key']).delete()
    },
    editGroup: function (target) {
      target.style.display = 'none'
      target.nextSibling.style.display = 'inline'
      target.nextSibling.focus()
    },
    saveGroup: function (id, target, subject) {
      const element = db.collection('groups').doc(id)
      return element.update(subject, target.value).then(() => {
        target.style.display = 'none'
        target.previousSibling.style.display = 'inline'
      })
    }
  },
  computed: {
  },
  firestore () {
    return {
      groups: db.collection('groups')
    }
  },
  data () {
    return {
      groups: []
    }
  }
}
</script>

