<template>
  <div>
    <listNames
      :names="names"
      :listed="true"
      :only-man="false"
      :only-female="false"
      :enable-delete="false"
      :enable-draggable="false"
    />
  </div>
</template>

<script>
import ListNames from '@/components/ListNames.vue'
import { db } from '@/firebase'
import { mapState } from 'vuex'

export default {
