<template>
  <ul class="nameList columnView">
    <!-- <ul class="nameList rowView"> -->
    <li
      v-for="birthday in sortOnCurrentMonth"
      :key="birthday.id"
      :class="$d(new Date(birthday.birthday), 'short', 'en')"
    >
      <span class="nameList__days">
        {{ $tc('days_till', daysToBirthday(birthday)) }}
      </span>
      <span class="nameList__sex">
        <RoundedButtonComponent
          :sex="birthday.sex"
          icon="male"
          oppo="F"
          :sex-key="birthday['.key']"
        />
        <RoundedButtonComponent
          :sex="birthday.sex"
          icon="female"
          oppo="M"
          :sex-key="birthday['.key']"
        />
      </span>
      <span class="nameList__name">
        <span
          @click="editField($event.target)"
        >{{ birthday.name || '-' }} </span>
        <input
          v-model="birthday.name"
          style="display: none"
          v-autowidth="{maxWidth: '960px', minWidth: '20px'}"
          @keydown.enter="saveField(birthday['.key'], $event.target, 'name')"
          @blur="saveField(birthday['.key'], $event.target, 'name')"
        >
        <span
          @click="editField($event.target)"
        >{{ birthday.lastname || '-' }}</span>
        <input
          v-model="birthday.lastname"
          style="display: none"
          v-autowidth="{maxWidth: '960px', minWidth: '0px'}"
          @keydown.enter="saveField(birthday['.key'], $event.target, 'lastname')"
          @blur="saveField(birthday['.key'], $event.target, 'lastname')"
        >
      </span>
      <span class="nameList__address">
        <span @click="editField($event.target)">
          {{ birthday.address || '-' }}
        </span>
        <input
          :value="birthday.address"
          v-autowidth="{maxWidth: '960px', minWidth: '20px'}"
          style="display: none"
          @keydown.enter="saveField(birthday['.key'], $event.target, 'address')"
          @blur="saveField(birthday['.key'], $event.target, 'address')"
        >
      </span>
      <span
        :class="$d(new Date(birthday.birthday), 'monthOnly', 'en')"
        class="nameList__birthday"
      >
        <span @click="editField($event.target)">
          {{ $d(new Date(birthday.birthday), 'short') }}
        </span>
        <input
          :value="birthday.birthday"
          v-autowidth="{maxWidth: '960px', minWidth: '140px'}"
          style="display: none"
          type="date"
          @keydown.enter="saveField(birthday['.key'], $event.target, 'birthday')"
          @blur="saveField(birthday['.key'], $event.target, 'birthday')"
        >
      </span>
      <span class="nameList__age alignCenter">
        <span :class="birthday.birthday | specialDate">{{ birthday.birthday | getAge }}</span>
      </span>
      <span class="nameList__delete alignRight buttons-container">
        <div
          class="rounded-button"
          @click="changeListed(userId, birthday['.key'], checkIfIsListed(userId, birthday['.key']))"
        >
          <font-awesome-icon
            v-if="!checkIfIsListed(userId, birthday['.key'])"
            icon="plus"
          />
          <font-awesome-icon
            v-if="checkIfIsListed(userId, birthday['.key'])"
            icon="minus"
          />
        </div>
        <div
          class="rounded-button"
          v-if="enableDelete"
          @click="deleteField(birthday)"
        >
          <font-awesome-icon icon="trash-alt" />
        </div>
        <div
          class="rounded-button"
          :class="{ active : listedStore.indexOf(birthday['.key']) >= 0 }"
          @click="selectBirthday(birthday['.key'])"
        />
      </span>
    </li>
  </ul>
</template>

<script>
import { db } from '@/firebase'
import RoundedButtonComponent from '@/components/RoundedButtonComponent.vue'
import store from '../store.js'
import { mapState } from 'vuex'

export default {
  name: 'ListNames',
  components: {
    RoundedButtonComponent
  },
  filters: {
    specialDate (value) {
      if (value) {
        let year = value.split('-')[0]
        let monthBirthday = parseInt(value.split('-')[1])
        let dayBirthday = parseInt(value.split('-')[2])
        let currentMonth = new Date().getMonth() + 1
        let currentDay = new Date().getDate()
        let thisYear = new Date().getFullYear()
        let difYears = thisYear - year
        if (monthBirthday < currentMonth) {
          difYears += 1
        } else if (monthBirthday === currentMonth) {
          if (dayBirthday < currentDay) {
            difYears += 1
          }
        }
        if (difYears % 5 === 0 || difYears === 1) {
          return 'special'
        }
      } else {
        return ''
      }
    },
    getAge (value) {
      if (value) {
        let year = value.split('-')[0]
        let monthBirthday = parseInt(value.split('-')[1])
        let dayBirthday = parseInt(value.split('-')[2])
        let currentMonth = new Date().getMonth() + 1
        let currentDay = new Date().getDate()
        let thisYear = new Date().getFullYear()
        let difYears = thisYear - year

        if (monthBirthday < currentMonth) {
          difYears += 1
        } else if (monthBirthday === currentMonth) {
          if (dayBirthday < currentDay) {
            difYears += 1
          }
        }
        return difYears
      }
    }

  },
  data () {
    return {
      selected: []
    }
  },
  props: {
    listed: {
      type: Boolean,
      required: true
    },
    names: {
      type: Array,
      required: true
    },
    enableDelete: {
      type: Boolean,
      required: true
    },
    enableDraggable: {
      type: Boolean
    }
  },
  computed: {
    sortOnCurrentMonth: function () {
      const sorter = (a, b) => this.daysToBirthday(a) < this.daysToBirthday(b) ? -1 : this.daysToBirthday(a) > this.daysToBirthday(b) ? 1 : 0
      const sortedOnMonths = this.names.slice(0).sort(sorter)
      return sortedOnMonths
    },
    listedStore: function () {
      return store.state.selected
    },
    ...mapState({
      userId: state => state.userId
    })
  },
  firestore () {
    return {
      listedList: db.collection('listed').doc(this.userId)
    }
  },
  methods: {
    deleteField: function (name) {
      db.collection('names').doc(name['.key']).delete()
    },
    editField: function (target) {
      target.style.display = 'none'
      target.nextSibling.style.display = 'inline'
      target.nextSibling.focus()
    },
    saveField: function (id, target, subject) {
      const element = db.collection('names').doc(id)
      return element.update(subject, target.value).then(() => {
        target.style.display = 'none'
        target.previousSibling.style.display = 'inline'
      })
    },
    changeSex: function (id, subject) {
      subject === 'F' ? subject = 'M' : subject = 'F'
      return db.collection('names').doc(id).update({
        sex: subject
      })
    },
    changeListed: function (id, retValue, check) {
      if (!check) {
        const listedValue = {
          [retValue]: true
        }
        db.collection('listed').doc(id).set(listedValue, { merge: true })
      } else {
        const listedValue = {
          [retValue]: false
        }
        db.collection('listed').doc(id).set(listedValue, { merge: true })
      }
    },
    checkIfIsListed: function (userId, name) {
      if (this.listedList[name]) {
        return true
      } else {
        return false
      }
    },
    daysToBirthday (value) {
      let monthBirthday = parseInt(value.birthday.split('-')[1])
      let dayBirthday = parseInt(value.birthday.split('-')[2])
      let currentMonth = new Date().getMonth() + 1
      let currentDay = new Date().getDate()
      let countYear = new Date().getFullYear()
      if (monthBirthday < currentMonth) {
        countYear += 1
      } else if (monthBirthday === currentMonth) {
        if (dayBirthday < currentDay) {
          countYear += 1
        }
      }

      let date2 = new Date(countYear, monthBirthday - 1, dayBirthday)
      let date1 = new Date(
        new Date().getFullYear(),
        currentMonth - 1,
        new Date().getDate()
      )

      var res = Math.abs(date1.getTime() - date2.getTime()) / 1000
      var days = Math.floor(res / 86400)
      return days
    },
    selectBirthday (value) {
      console.log(value)
      store.getters.getListed.indexOf(value) === -1 ? store.commit('addListed', value) : store.commit('removeListed', store.getters.getListed.indexOf(value))
      store.commit('listed', store.getters.getListed)
      return store.getters.getListed.indexOf(value)
    }
  }
}
</script>

