  <div class="home">
    <div class="topOptions">
      <FilterComponent />
      <BulkActions />
    </div>
    <AddNames />
    <listNames
      :names="names"
      :listed="false"
      :enable-delete="true"
      :enable-draggable="false"
    />
  </div>
</template>

<script>
import AddNames from '@/components/AddNames.vue'
import ListNames from '@/components/ListNames.vue'
import BulkActions from '@/components/BulkActions.vue'
import FilterComponent from '@/components/FilterComponent.vue'

import { db } from '@/firebase'

export default {
  name: 'Home',
  components: {
    AddNames,
    BulkActions,
    ListNames,
    FilterComponent
  },
  data () {
    return {
      names: []
    }
  },
  computed: {
    showManOnly () {
      return this.$store.getters.showManOnly
    },
    showFemaleOnly () {
      return this.$store.getters.showFemaleOnly
    }

  },
  firestore () {
    return {
      names: db.collection('names')
    }
  }
}
</script>

