<script>
import { db } from '@/firebase'
// import { CsvImport } from '@/components/CsvImport.vue'

export default {
  name: 'AddNames',
  components: {
    // CsvImport
  },
  data () {
    return {
      names: [],
      newName: null,
      newLastname: null,
      newSex: 'M',
      newBirthday: null,
      newAddress: null
    }
  },
  firestore () {
    return {
      names: db.collection('names')
    }
  },
  computed: {

  },
  methods: {
    addName: function (element = null) {
      if (element) {
        if (element.name && element.lastname && element.sex && element.birthday && element.address) {
          db.collection('names').add({
            name: element.name,
            lastname: element.lastname,
            sex: element.sex,
            birthday: element.birthday,
            month: element.birthday.split('-')[1],
            address: element.address,
            timestamp: new Date()
          })
        } else {
          if (this.newName && this.newLastname && this.newSex && this.newBirthday && this.newAddress) {
            db.collection('names').add({
              name: this.newName,
              lastname: this.newLastname,
              sex: this.newSex,
              birthday: this.newBirthday,
              month: this.newBirthday.split('-')[1],
              address: this.newAddress,
              timestamp: new Date()
            })
            this.newName = ''
            this.newLastname = ''
            this.newSex = 'M'
            this.newBirthday = ''
            this.newAddress = ''
          } else {
            console.error('Fill in all the fields dummy')
          }
        }
      }
    },
    deleteName: function (name) {
      db.collection('names').doc(name['.key']).delete()
    },
    saveData: function () {
      this.csv.forEach((element) => {
        console.log('element', element)
        this.addName(element)
      })
    }
  }
}
