<template>
  <div class="locale-changer link-list__item">
    <select
      class="locale-changer-select"
      v-model="$i18n.locale"
      hidden
    >
      <option
        v-for="(lang, i) in langs"
        :key="`Lang${i}`"
        :value="lang"
      >
        {{ lang }}
      </option>
    </select>
    <div
      class="lang-changer"
    >
      <div
        :class="{ active: selectedLang === 'nl' }"
        class="lang-changer__option"
        @click="langChanger('nl')"
      >
        NL
      </div>
      <div class="lang-changer__devider">
        /
      </div>
      <div
        class="lang-changer__option"
        :class="{active: selectedLang === 'en'}"
        @click="langChanger('en')"
      >
        EN
      </div>
    </div>
  </div>
</template>

<script>
import store from '../store'

export default {
  name: 'ChangeLang',
  data () {
    return {
      langs: ['nl', 'en'],
      selectedLang: store.state.preferredLanguage
    }
  },
  methods: {
    langChanger: function (lang) {
      document.querySelector('.locale-changer-select').value = lang
      this.$i18n.locale = lang
      this.selectedLang = lang
      document.documentElement.setAttribute('lang', lang)
    }
  }
}
</script>

<style lang="scss">
.lang-changer {
  display: flex;
  justify-content: space-between;
  &__option {
    cursor: pointer;
    &:hover,
    &.active {
      color: #00a28a;
    }
  }
  &__devider {
    margin: 0 0.2em;
  }
}
</style>
