<template>
  <form @submit="handleLogin">
    <input
      type="text"
      placeholder="Email"
      v-model="email"
    >
    <input
      type="password"
      placeholder="Password"
      v-model="password"
    >
    <button type="submit">
      Login
    </button>
    <span>Or go back to <router-link to="/register">Register</router-link>.</span>
  </form>
</template>
<script>
export default {
  name: 'Login',
  data () {
    return {
      email: '',
      password: ''
    }
  },
  methods: {
    handleLogin () {
      const email = this.email
      const password = this.password
      this.$store.dispatch('login', { email, password })
        .then((user) => {
          this.$router.replace('home')
        })
        .catch((err) => {
          console.log(err)
        })
    }
  }
}
</script>
