import Vue from 'vue'
import './plugins/fontawesome'
import App from './App.vue'
import router from './router'
import store from './store'
import VueFirestore from 'vue-firestore'
import {
  library
} from '@fortawesome/fontawesome-svg-core'
import {
  fas
} from '@fortawesome/free-solid-svg-icons'
import {
  far
} from '@fortawesome/free-regular-svg-icons'
import {
  fab
} from '@fortawesome/free-brands-svg-icons'
import Raven from 'raven-js'
import RavenVue from 'raven-js/plugins/vue'
import VueInputAutowidth from 'vue-input-autowidth'
import VueI18n from 'vue-i18n'
import Messages from './translations/translation'
import DateTimeFormats from './translations/DateTimeFormats'
import {
  VueCsvImport
} from 'vue-csv-import'
import 'firebase/auth'
import { firebase } from '@firebase/app'
import { db } from '@/firebase'

Raven
  .config('https://3e0de6be5bf342df97422eaa8dfb4395@sentry.io/1446817')
  .addPlugin(RavenVue, Vue)
  .install()

library.add(fas, far, fab)

Vue.use(VueI18n)
Vue.use(VueFirestore)
Vue.use(VueInputAutowidth)
Vue.use(VueCsvImport)
Vue.config.productionTip = false

const i18n = new VueI18n({
  locale: store.state.preferredLanguage, // set locale
  fallbackLocale: 'nl',
  messages: Messages,
  dateTimeFormats: DateTimeFormats
})

console.log(store.state.preferredLanguage)

i18n.locale = store.state.preferredLanguage
let app = ''

firebase.auth().onAuthStateChanged((user) => {
  if (user) {
    store.commit('SET_USER_ID', user.uid)
    store.commit('SET_USER_DISPLAY_NAME', user.email)
    const people = db.collection('people').doc(user.uid)
    console.log(people)
    people.get()
      .then(doc => {
        console.log(doc.data().preferredLanguage)
        store.commit('SET_USER_PREFERRED_LANGUAGE', doc.data().preferredLanguage)
      })
      .catch(err => {
        console.log('Error getting document', err)
      })
  } else {
    store.commit('SET_USER_ID', '')
    store.commit('SET_USER_DISPLAY_NAME', '')
  }
  if (!app) {
    app = new Vue({
      router,
      store,
      i18n,
      render: h => h(App)
    }).$mount('#app')
  }
})
