import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/HomePage.vue'
import List from './views/ListPage.vue'
import Groups from './views/GroupsPage.vue'
import Information from './views/InformationPage.vue'
import User from './views/UserPage.vue'
import Register from './views/RegisterPage.vue'
import Login from './views/LoginPage.vue'
import { firebase } from '@firebase/app'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '*',
      redirect: '/login'
    },
    {
      path: '/',
      redirect: '/login'
    },
    {
      path: '/list',
      name: 'list',
      component: List,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/groups',
      name: 'groups',
      component: Groups,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/information',
      name: 'information',
      component: Information
    },
    {
      path: '/user',
      name: 'user',
      component: User,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/register',
      name: 'register',
      component: Register
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/home',
      name: 'home',
      component: Home,
      meta: {
        requiresAuth: true
      }
    }
  ]
})

router.beforeEach((to, from, next) => {
  const currentUser = firebase.auth().currentUser
  const requiresAuth = to.matched.some(record => record.meta.requiresAuth)

  if (requiresAuth && !currentUser) {
    next('login')
  } else {
    next()
  }
})

export default router
